import axios from 'axios'
import wx from './wechat/wechat-param'
import router from '../router'
import ToastPlugin from 'vux/src/plugins/toast/index.js'

import Vue from 'vue'
import wxLogin from './wechat/wechat-login'
import wxData from './wechat/wechat-param'


Vue.prototype.$vux = ToastPlugin;


let upBaseUrl = '';
if (process.env.NODE_ENV === 'production') {


upBaseUrl = 'http://preywapi.fenganghr.com/';

}
if (process.env.NODE_ENV === 'development') {

upBaseUrl = 'http://preywapi.fenganghr.com/';



}
const instance = axios.create({

baseURL: upBaseUrl,


timeout: 20000,
headers:{

"Content-Type":'application/json',
},

})
instance.interceptors.request.use(

config => {

config.headers = {
"Authorization":localStorage.getItem('token')||'',

"backend":'lhyg'
}
return config;
},
error => {
return Promise.reject(error);
}

);
instance.interceptors.response.use(function (response) {

if(response.data.code==401){
console.log(response.data.code,'if401');
localStorage.clear();
setTimeout(()=>{
wxLogin(wxData);
},200)







}else if (response.data.code==404){
console.log(error.response.message)
Vue.$vux.toast.show({
text: error.response.message,
type:'text'
})
return Promise.reject(error.response.message)

}else{
return response

}
}, function (error) {

console.log(error,'000000000000009')





})

export  {
instance,
upBaseUrl
}



